.Header {
    background-color: #202020;
  }
  
  .app-footer {
    background-color: rgba(255, 255, 255, 0.6);
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    text-align: center;
  }
  
  .footer {
    color: firebrick;
    cursor: pointer;
    display: inline-block;
    padding: 0px 10px 40px 10px;
    text-decoration: underline;
  }
  
  .footer-top {
    color: firebrick;
    cursor: pointer;
    display: block;
    padding-top: 40px;
  }
  
  .footer:hover {
    color: white;
  }